.cart-header {
    display: flex;
    z-index: 3;
    flex-direction: column;
    position: absolute;
    width: 500px;
    height: max-content;
    background-color: #e1e7ed;
    border-radius: 10px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .2);
    right: 5%;
    top: 15%;
    border: 1px solid #c3c9cf;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transition: opacity .3s linear, visibility .3s linear;
}

.favourite-header {
    display: flex;
    z-index: 3;
    flex-direction: column;
    position: absolute;
    width: 500px;
    height: max-content;
    background-color: #e1e7ed;
    border-radius: 10px;
    box-shadow: 0 4px 4px rgba(0, 0, 0, .2);
    right: 18%;
    top: 30%;
    border: 1px solid #c3c9cf;
    visibility: hidden;
    opacity: 0;
    overflow: hidden;
    transition: opacity .3s linear, visibility .3s linear;
}

.product-counter {
    font-weight: 600;
    font-size: 20px;
    margin-left: 10px;
}

.cart-product {
    margin: 10px 10px;
    border-radius: 10px;
    height: 120px;
    background-color: white;
    display: flex;
}

.cart-product-img {
    height: 100px;
    width: 80px;
    padding: 10px 10px;
}

.cart-product-description {
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 212.66px;
}

.cart-header-main {
    height: max-content;
}

.cart-product-name {
    font-weight: 600;
    margin-top: 10px;
}

.cart-product-color {
    font-size: 12px;
}

.cart-product-price {
    font-size: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
}

.cart-header-2 {
    height: 40px;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid black;
    align-items: center;
    margin: 0 10px 0 10px;
}

.cart-delete-button {
    margin-right: 10px;
    cursor: pointer;
}

.cart-price {
    margin-top: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cart-price svg {
    width: 25px;
    height: 25px;
    margin-left: 11px;
    cursor: pointer;
}

.cart-right-space {
    flex-wrap: nowrap;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.cart-product-counter {
    display: flex;
    background-color: #c1bdbd;
    width: 80px;
    height: 25px;
    border-radius: 10px;
    align-items: center;
    margin-bottom: 10px;
    justify-content: space-between;
}

.cart-product-counter svg {
    height: 25px;
    cursor: pointer;
}

.product-counter-2 {
    font-weight: 600;
    font-size: 20px;
}

.cart-header-bottom {
    height: 100px;
    margin: 0 10px 0 10px;
    border-top: 1px solid;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.total-price {
    display: flex;
    justify-content: space-between;
    font-weight: 600;
    font-size: 20px;
    margin: 10px 10px 0 10px;
}

.asdfasdf {
    display: flex;
    justify-content: flex-end;
}

.cart-buy-button {
    cursor: pointer;
    display: flex;
    padding: 20px;
    font-weight: 700;
    font-size: 20px;
    margin-right: 10px;
    background-color: #F36223;
    width: 70px;
    height: 1px;
    color: white;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
}



