/* common styles */

html {
    scroll-behavior: smooth;
}

body {
    width: 1903px;
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    background-color: #F8FAFE;
    font-family: 'Inter', sans-serif;
}

textarea {
    outline: none;
}

a {
    text-decoration: none;
    color: #008CF0;
    transition: all .09s ease-in-out;
}

a:hover {
    color: #DA4A0C;
}

a:active {
    color: #A93A09;
}

ul {
    list-style-type: none;
    padding: 0;
}

.font-size-16 {
    font-size: 16px;
    font-weight: 500;
}

.font-size-32 {
    font-size: 32px;
    font-weight: 700;
}

.font-size-44 {
    font-size: 44px;
    font-weight: 700;
}

.title {
    font-size: 16px;
    font-weight: 700;
}

/* "header" styles */

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: sticky;
    top: 0;
    width: 100%;
    background-color: #F8FAFE;
    border-bottom: 1px solid black;
    z-index: 2;
}

.cart-logo {
    margin-right: 30px;
}

.cart {
    position: relative;
}

.cost {
    position: absolute;
    top: 5px;
}

.header {
    align-items: center;
    display: flex;
    gap: 10px;
    padding: 15px 0px 9px 51px;
}

/* main */

.flex-container {
    display: flex;
    flex-direction: column;
    padding: 0 51px;
}

.flex-box-container {
    flex-direction: row;
}

.main-box {
    justify-content: space-between;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 1750px;
}

.flex-container-position {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.navigation {
    margin-top: 20px;
    gap: 4px;
    display: flex;
}

.product-name {
    margin-top: 10px;
}

/* product-photo-slider */

.product-photo-slider {
    margin-top: 20px;
    display: flex;
    max-width: 300px;
    left: 40%;
}

.slick-prev {
    left: -40px;
}

.slick-prev:before, .slick-next:before {
    color: black;
    font-size: 30px;
}

.productImg {
    height: 387px;
    width: 300px;
    background-repeat: no-repeat;
    background-size: 100%;
}

/* color-photos */

.colors-photo {
    display: flex;
    gap: 10px;
}

.product-color-photo {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.product-color-photo img {
    height: 60px;
    padding: 20px;
    transition: all .09s ease-in-out;
}

/* product-feature */

.features-list {
    gap: 5px;
    display: flex;
    line-height: 19px;
}

.product-feature {
    margin-top: 10px;
    margin-bottom: 0;
}

.product-feature li {
    margin-bottom: 10px;
}

.product-feature img {
    margin-right: 5px;
}

/* description */

.description {
    display: flex;
    width: 880px;
    height: auto;
    flex-direction: column;
    gap: 10px;
}

.description__tagline {
    flex-wrap: wrap;
    width: 382px;
}

/* buycard */

.buycard {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 275px;
    width: 420px;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, .1);
    justify-content: center;
}

.price-tags {
    align-items: center;
    gap: 10px;
    display: flex;
    flex-direction: row;
    margin: 0;
}

.old-price {
    text-decoration: line-through;
    color: #888888;
    margin: 0;
}

.discount {
    padding: 4px 8px;
    border-radius: 500px;
    background-color: #F36223;
    color: white;
    margin: 0;
    font-weight: 500;
}

.new-price {
    font-weight: 500;
    font-size: 32px;
    margin-top: 5px;
    margin-bottom: 0;
}

.delivery-information {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    gap: 5px;
    margin-bottom: 20px;
}

.delivery-information__pickup {
    margin-bottom: 0;
}

.delivery-information_courier {
    margin-top: 2px;
}

.cart-logo-counter {
    width: 25px;
    height: 25px;
    position: absolute;
    right: 1%;
    top: 10%;
}

.favourite-logo {
    position: absolute;
    right: 15%;
    top: 27%;
}

.favourite-logo-counter {
    width: 25px;
    height: 25px;
    position: absolute;
    right: -20%;
    top: -30%;
}

.favouriteCounter {
    position: absolute;
    right: -3%;
    top: -20%;
    color: white;
}

.counter-22 {
    color: white;
    position: absolute;
    font-size: 15px;
    width: 20px;
    height: 20px;
    right: -12%;
    top: 20%;
    z-index: 100;
}

.add-to-cart button {
    cursor: pointer;
    padding: 20px 49px;
    background-color: #F36223;
    font-size: 20px;
    width: 100%;
    height: 64px;
    color: white;
    border-radius: 4px;
    border: none;
    transition: all .09s ease-in-out;
}

.add-to-cart button:hover {
    transform: scale(1.02);
    box-shadow: 0 4px 4px rgba(0, 0, 0);
    background-color: #DA4A0C;
}

.add-to-cart button:active {
    content: '';
    background-color: #A93A09;
}

.App {
    text-align: start;
}

.favourite:hover > .favourite_colors {
    transition: all .2s;
    box-shadow: 0 4px 4px rgba(0, 0, 0);
    fill: #DA4A0C;
}

.favourite:active > .favourite_colors {
    fill: #A93A09;
}

.svg {
    display: flex;
    position: absolute;
    top: 40px;
    right: 40px;
}

/* flex-positions */

.left-position {
    display: flex;
    flex-direction: column;
    order: 0;
}

.buycard-ad-position {
    display: flex;
    flex-direction: column;
    order: 1;
}

.pseudo-button {
    display: flex;
    top: 0;
    right: 0;
    position: absolute;
}

/* ad */

.ad {
    order: 3;
    display: flex;
    flex-direction: column;
    width: 420px;
    height: 642px;
}

.ad_title {
    color: #888888;
    font-size: 14px;
}

.ad__box {
    color: #888888;
    font-size: 14px;
    border: dashed;
    border-color: #888888;
    margin-bottom: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 420px;
    height: 300px;
    background-color: rgba(240, 240, 240, 1);
}

/* product-color-photo__buttons */

.product-color-photo__button > input {
    visibility: hidden;
    position: absolute;
}

.product-color-photo__button > input + img {
    cursor: pointer;
    border: 1px solid #888888;
    border-radius: 4px;
}

.product-color-photo__button > input:checked + img {
    border: 1px solid #F36223;
}

.product-color-photo__button > input:hover + img:hover {
    background: rgba(0, 0, 0, .2);
}
.product-color-photo__button > input:active + img {
    content: '';
    border: 1px solid #A93A09;
}

/* memory */

.memory {
    margin-top: 10px;
    gap: 10px;
    display: flex;
    height: 39px;
}

.memory-button > input {
    visibility: hidden;
    position: absolute;
}

.memory-button > input + b {
    cursor: pointer;
    padding: 10px;
    border: solid;
    border-width: 1px;
    border-radius: 4px;
    background-color: white;
    border-color: #888888;
    transition: all .09s ease-in-out;
}

.memory-button > input:checked + b {
    border: 1px solid #F36223;
}

.memory-button > input:hover + b {
    transform: scale(1.02);
    box-shadow: 0 2px 3px rgba(0, 0, 0);
}

.memory-button > input:active + b {
    border: 1px solid #A93A09;
}

/* reviews-block */

.reviews-container {
    display: flex;
    flex-direction: column;
    height: auto;
    width: 880px;
    margin-top: 40px;
    gap: 20px;
    margin-bottom: 40px;
}

.reviews__header {
    display: flex;
    justify-content: space-between;
}

.stars-photo img {
    width: 200px;
    height: 40px;
    order: 2;
}

.reviewer-photo img {
    width: 140px;
    height: 140px;
    border-radius: 100px;
}

.reviews-block {
    margin-top: 20px;
    display: flex;
    gap: 20px;
}

.first {
    padding-bottom: 40px;
    border-bottom: 1px dashed;
}

.second {
    border: none;
}

.reviews-main-information {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.reviewer-name {
    font-size: 20px;
}

.counter {
    color: #888888;
}

/* table-container */

.table {
    margin: 50px 0 50px 0;
    border-collapse: collapse;
    border: 1px solid;
    table-layout: fixed;
    width: 876px;
    padding: 5px;
    text-align: center;
}

td {
    padding: 10px;
    border: 1px solid;
}

.table tbody tr:hover td {
    background-color: #F2F2F2;
}

/* footer */

.footer-container {
    padding: 20px 51px 20px 51px;
    display: flex;
    background-color: #F2F2F2;
    justify-content: space-between;
    align-items: center;
}

/* add-review-form */

.reviews-form__addbtn button {
    cursor: pointer;
    right: 0;
    width: 128px;
    padding: 10px;
    border: 1px solid;
    border-radius: 4px;
    color: black;
    transition: all .09s ease-in-out;
    background-color: white;
}

.reviews-form__addbtn button:hover {
    transform: scale(1.01);
    box-shadow: 0 2px 0px rgba(0, 0, 0);
}

.reviews-form {
    width: 876px;
    height: 880px;
    display: flex;
    flex-direction: column;
    background-color: white;
    border-radius: 10px;
    padding: 20px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, .1);
    margin-bottom: 40px;
}

.reviews-form__title {
    font-weight: 700;
    text-align: center;
    font-size: 36px;
}

.reviews-form__inf {
    margin-top: 20px;
    margin-left: 20px;
    display: flex;
    justify-content: space-between;
}

.reviews-form__text {
    margin-top: 20px;
    border-radius: 10px;
    height: 100%;
    padding: 20px;
    border: 1px solid #e0e0e0;
    transition: 0.3s;
}

.reviews-form__inf_group {
    position: relative;
    width: 200px;
}

.form-label {
    position: absolute;
    left: 0;
    top: 0;
    color: #9e9e9e;
    transition: 0.3s;
}

.form-input {
    padding: 0 0 10px 0;
    border: none;
    border-bottom: 1px solid #e0e0e0;
    background-color: transparent;
    outline: none;
    transition: 0.3s;
}

.form-input:focus {
    border-bottom: 1px solid #1a73a8;
}

.form-input:focus ~ .form-label,  
.form-input:not(:placeholder-shown) ~ .form-label {
    top: -18px;
    font-size: 12px;
    color: #e0e0e0;
}

.reviews-form__addbtn-nad {
    margin-top: 20px;
    display: flex;
    justify-content: flex-end;
}

.reviews-form__addbtn {
    border-radius: 5px;
    padding: 10px;
    border: none;
    font-weight: 700;
    background-color: #DA4A0C;
    color: white;
    transition: all .03s;
    cursor: pointer;
}

.reviews-form__addbtn:hover {
    transform: scale(1.03);
}

/* warning form */

.warning {
    padding: 5px;
    background-color: #DA4A0C;
    align-items: center;
    color: white;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    font-size: 10px;
}

.def-page {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 780px;
}