@media only screen and (max-width:1500px) and (min-width:1024px) {
    body {
        width: max-content;
        margin-left: 0;
    }

    .flex-container {
        padding: 0 15px 0 0px;
    }

    .product-photo img {
        height: 262px;
    }

    .table-container {
        display: none;
    }

    .buycard {
        width: 360px;
        height: 313px;
    }

    .cart-logo-counter {
        top: 5%;
        right: 1.8%;
    }

    .cost {
        top: 15px;
    }

    .product-color-photo {
        width: 500px;
    }

    .header {
        padding: 15px 15px 9px 15px;
    }

    .description {
        width: 500px;
    }

    main {
        padding: 0 0px 0 15px;
    }

    .footer-container {
        padding: 30px 15px 30px 15px;
    }



}

@media only screen and (max-width:1024px) and (min-width: 360px) {
    body {
        width: 360px;
        margin: 0;
    }

    .logos {
        display: none;
    }

    .productImg {
        display: none;
    }

    .product-photo-slider {
        content: url(../../../public/img/image-1.webp);
    }

    .cart-logo-counter {
        top: 5%;
        right: 4.5%;
    }

    .cart-header {
        display: none !important;
    }

    .header__company-name {
        font-size: 32px;
    }

    .product-color-photo

    .header {
        padding: 5px 5px 5px 5px;
        width: 330px;
    }

    .main-box {
        justify-content: center;
        flex-direction: column;
    }

    .left-position {
        order: 1;
    }

    .navigation {
        margin-top: 5px;
        width: 350px;
        flex-wrap: wrap;
    }

    .colors-photo {
        display: flex;
        width: 360px;
        flex-wrap: wrap;
    }

    .buycard {
        width: 300px;
    }

    .product-photo-slider {
        left: 5%;
    }

    .add-to-cart button {
        padding: 0;
    }

    .ad {
        display: none;
    }

    .table-container {
        display: none;
    }

    .warning {
        width: 265px;
    }

    .description {
        width: 350px;
        flex-wrap: wrap;
    }
    
    .buycard-ad-position {
        order: 0;
    }

    .reviews-block {
        flex-direction: column;
        flex-wrap: wrap;
    }

    .title {
        width: 300px;
    }

    .memory {
        width: 300px;
    }

    .reviews-container {
        width: 350px;
    }

    .description__tagline {
        width: 350px;
    }

    .reviews-form {
        width: 300px;
        height: 380px;
    }

    .reviews-form__inf_group {
        margin-bottom: 20px;
    }

    .form-input {
        width: 265px;
    }

    .email {
        display: none;
    }

    .slick-prev,
    .slick-next {
        display: none !important;
    }

    .reviews-form__inf {
        justify-content: center;
        flex-direction: column;
    }

    .features-list {
        width: 350px;
        flex-wrap: wrap;
    }

    .flex-container {
        padding: 10px;
    }

    .footer-container {
        padding: 10px;
        flex-direction: column;
        align-items: baseline;
    }

    .yakor {
        margin-top: 10px;
    }
}